.Container,.Body
{
    padding:5px;
    margin:5px;
    margin-top: 0;
    padding-top: 0;
}

h1, p{
    margin-top: 0;
}

.Body
{
    font-family: 'Merienda','Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    text-align: center;
    font-size: 20px;
    background-color: white;
    margin-top: 0;
}
.body{
    background-color: white;
    height: 100vh;
}
.base{
    height: 40vh;
    background-color: #1A0325;
    width: 100vw;
    position: fixed;
    bottom: 0;
    left: 0;
}
.tests{
    background: #F1F1F1;
    box-shadow: -6px 0px 4px rgba(0, 0, 0, 0.25), 6px 0px 4px rgba(0, 0, 0, 0.25);
    border-radius: 8px;
    text-align: center;
    display: flex;
    margin: 0 10%;
    z-index: 1000;
    flex-wrap: wrap;
    justify-content: center;
    transform: translateY(-40%)
}
.tests a{
    padding: 15px 20px;
    background: #1A0325;
    border-radius: 8px;
    margin: 20px 20px;
    color: white;
    text-decoration: none;
}

.tests a:hover
{
    box-shadow: 0 0 5px 5px #aaa7a7;
}

footer{
    position: fixed;
    bottom: 0;
    left: 25%;
    right: 25%;
    color: white;
    text-align: center;
    padding-bottom: 20px;
}
.logo
{
    width:40%;
    /* position: absolute; */
    height: 14%;
    /* float: right; */
    /* margin-top: 20px; */
}

@media (min-width:768px)
{
  
    .body
    {
        background: linear-gradient(90deg, white 70%, #1A0325 70%);
        position: relative;
    }

    .Body
    {
        position: absolute;
        width:60vw;
        top:15vh;
        padding:20px;
    }

    .tests
    {
        position: fixed;
        top:50vh;
        left:10vw;
        width:400px;
    }

    .base
    {
        
        height: 0;
        width: 0;
    }

    footer
    {
        color:white;
        position:absolute;
        bottom: 20px;
        left:65%;
        width: 35%;
       
    }

    .logo
    {
        width:30%;
        /* position: absolute; */
        height: 9%;
        float: right;
        margin-top: 50px;
    }



}